.ratingselect .rating-type{font-size:0;margin:0 18px;padding:18px 0;position:relative}.ratingselect .rating-type::after{position:absolute;left:0;bottom:0;width:100%;border-top:1px solid rgba(7,17,27,0.1);display:block;content:' '}.ratingselect .rating-type .block{display:inline-block;vertical-align:top;font-size:12px;line-height:16px;padding:8px 12px;margin-right:8px;color:#4d555d;border-radius:1px}.ratingselect .rating-type .block .count{font-size:8px;margin-left:2px}.ratingselect .rating-type .block.active{color:#fff}.ratingselect .rating-type .block.positive,.ratingselect .rating-type .block.all{background-color:rgba(0,160,220,0.2)}.ratingselect .rating-type .block.positive.active,.ratingselect .rating-type .block.all.active{background-color:#00a0dc}.ratingselect .rating-type .block.negative{background-color:rgba(77,85,93,0.2)}.ratingselect .rating-type .block.negative.active{background-color:#4d555d}.ratingselect .switch{color:#93999f;font-size:0;padding:12px 0;border-bottom:1px solid rgba(7,17,27,0.2);padding-left:18px}.ratingselect .switch.on .icon-check_circle{color:#00b43c}.ratingselect .switch .icon-check_circle{display:inline-block;vertical-align:top;font-size:24px;line-height:24px;color:#93999f;margin-right:4px}.ratingselect .switch .text{display:inline-block;vertical-align:top;font-size:12px;line-height:24px}



