body,html {
    line-height: 1;
    font-weight: 200;
    font-family: 'PingFang SC','STHeitiSC-Light','Helvetica-Light','sans-serif';
}

.cf {
    display: inline-block;
    &::after {
        content: ".";
        display: block;
        height: 0;
        line-height: 0;
        clear: both;
        visibility: hidden;
    }
}



@media (-webkit-min-device-pixel-ratio: 1.5),(min-device-pixel-ratio: 1.5){
    .border-1px {
        &::after {
            -webkit-transform: scaleY(.7);
            transform: scaleY(.7);
        }
    }
}

@media (-webkit-min-device-pixel-ratio: 2),(min-device-pixel-ratio: 2){
    .border-1px {
        &::after {
            -webkit-transform: scaleY(.5);
            transform: scaleY(.5);
        }
    }
}




